[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.countries-list{margin:0 auto;width:1440px}@media screen and (max-width: 1440px){.countries-list{max-width:1280px}}@media screen and (max-width: 1280px){.countries-list{max-width:768px}}@media screen and (max-width: 768px){.countries-list{width:480px}}@media screen and (max-width: 500px){.countries-list{width:320px}}.countries-list .countries-list__wrapper{height:auto;margin:0 2em;padding:.75em;display:grid;gap:70px;grid-template-columns:repeat(4, 1fr)}@media screen and (max-width: 1440px){.countries-list .countries-list__wrapper{gap:50px}}@media screen and (max-width: 1280px){.countries-list .countries-list__wrapper{margin:0;grid-template-columns:repeat(3, 1fr)}}@media screen and (max-width: 768px){.countries-list .countries-list__wrapper{padding:0;grid-template-columns:530px}}@media screen and (max-width: 500px){.countries-list .countries-list__wrapper{grid-template-columns:320px}}.countries-list .countries-list__button-more{width:160px;margin:2em 0;margin-left:calc(50% - 80px);padding:.5em 0;border:none;font-family:'Nunito Sans', sans-serif;font-size:1.2em;font-weight:400;color:var(--text);outline:1px solid var(--shadow);border-radius:6px;transition:.1s;color:var(--text);background-color:var(--element-bg)}.countries-list .countries-list__button-more:hover{opacity:.5;cursor:pointer}
