[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.countries-item{width:264px;box-shadow:0px 0px 10px var(--shadow);background-color:var(--element-bg);outline:1px solid var(--shadow);border-radius:6px}@media screen and (max-width: 1440px){.countries-item{width:240px}}@media screen and (max-width: 1280px){.countries-item{width:210px}}@media screen and (max-width: 768px){.countries-item{width:480px}}@media screen and (max-width: 500px){.countries-item{width:320px}}.countries-item .countries-item__image{width:100%;height:160px;outline:1px solid var(--shadow);border-radius:6px 6px 0 0}.countries-item .countries-item__image:hover{cursor:pointer}@media screen and (max-width: 768px){.countries-item .countries-item__image{height:300px}}@media screen and (max-width: 500px){.countries-item .countries-item__image{height:220px}}.countries-item .countries-item__title{text-decoration:none;font-size:20px;line-height:20px;font-weight:600;color:var(--text)}.countries-item .countries-item__title>p{margin:.75em 1em}@media screen and (max-width: 768px){.countries-item .countries-item__title{font-size:36px;line-height:36px}}@media screen and (max-width: 500px){.countries-item .countries-item__title{font-size:28px;line-height:28px}}.countries-item .countries-item__info{padding:.25em 1.1em;font-size:16px;line-height:16px;font-weight:300;color:var(--text)}.countries-item .countries-item__info:last-of-type{padding-bottom:3em}@media screen and (max-width: 768px){.countries-item .countries-item__info{font-size:28px;line-height:28px}}@media screen and (max-width: 500px){.countries-item .countries-item__info{font-size:22px;line-height:22px}}
