[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.filter-container{max-width:1440px;margin:80px auto 0;display:flex;align-items:center;justify-content:space-between}@media screen and (max-width: 1440px){.filter-container{max-width:1280px}}@media screen and (max-width: 1280px){.filter-container{max-width:768px}}@media screen and (max-width: 768px){.filter-container{max-width:500px;flex-direction:column}}.filter-container .filter-container__form{position:relative;width:480px;margin:2em;position:relative}@media screen and (max-width: 500px){.filter-container .filter-container__form{max-width:320px}}.filter-container .filter-container__form .filter-container__search{width:100%;padding:.75em;border:none;font-size:20px;line-height:20px;box-shadow:0px 0px 6px var(--shadow);outline:1px solid var(--shadow);border-radius:6px}@media screen and (max-width: 500px){.filter-container .filter-container__form .filter-container__search{width:320px}}.filter-container .filter-container__form .filter-container__button-reset{position:absolute;top:3px;right:3px;position:absolute;width:48px;height:48px;border:none;box-shadow:0px 0px 6px var(--shadow);background-color:var(--element-bg);font-family:'Nunito Sans', sans-serif;font-size:1.3em;font-weight:800;text-decoration:none;color:var(--text);outline:1px solid var(--shadow);border-radius:6px}.filter-container .filter-container__form .filter-container__button-reset:hover{cursor:pointer}.filter-container .filter-container__form .filter-container__button-reset:disabled{cursor:default;opacity:.4}
